import React from 'react'
import Layout from '../core/Layout'
import {isAuthenticated} from '../auth'
import {Link} from 'react-router-dom'

const Dashboard = () => {

    const {user: {_id, name, email, role}} = isAuthenticated();



    const userLinks = () => {
        return (
            
            <div className="card">
                <h4 className="card-header text-warning" style={{backgroundColor: 'black'}}>User Links</h4>
                <ul className="list-group">
                   
                    <li className="list-group-item">
                        <Link className="nav-link" to={`/profile/${_id}`}>
                            Update Profile
                        </Link>
                    </li>
                    <li className="list-group-item">
                        <Link className="nav-link" to={'/join'}>
                            JoinNow
                        </Link>
                    </li>
                </ul>
            </div>
        );
    };

    const userInfo = () => {
        return (
            <div className="card mb-9">
                <h3 className="card-header text-warning" style={{backgroundColor: 'black'}}>User Information</h3>
                <ul className="list-group">
                    <li className="list-group-item">{name}</li>
                    <li className="list-group-item">{email}</li>
                    <li className="list-group-item">
                        {role === 1 ? "Admin" : "Registered User"}
                    </li>
                </ul>
            </div>
        );
    };


    
    

    return (
      
             <div className="row">
                <div className="col-3" style={{marginTop: '30px', marginLeft: "100px"}}>{userLinks()}</div>
                <div className="col-6" style={{marginTop: '30px', marginLeft: "100px"}}>
                    {userInfo()}
                  
                </div>
            </div>
            


        
    );
};

export default Dashboard;